@import url("https://fonts.googleapis.com/css2?family=Irish+Grover&family=Italiana&family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
}

body {
  background-color: #111;
}

.main-container {
  display: flex;
}

.container {
  width: 50%;
  min-height: 100vh;
}

.first-container {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.content-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.song-image {
  width: 25rem;
  height: 25rem;
  border-radius: 1.5rem;
  object-fit: cover;
}

.music-editor {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.play-button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  width: 50px;
  height: 50px;
  padding: 10px;
  border: none;
  border-radius: 50%;
}

.play-image {
  width: 25px;
  height: 25px;
}

.song-data {
  margin-top: 15rem;
  color: #fff;
  font-family: "Poppins", sans-serif;
}

.song-title {
  font-size: 4rem;
  font-weight: 800;
}

.song-artist {
  color: rgb(156, 156, 156);
  font-family: "Poppins", sans-serif;
  font-weight: 100;
  font-size: 1.5rem;
}

.song-artist a {
  color: #fff;
}

.lyrics-container {
  box-shadow: rgba(85, 85, 85, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  margin-top: 7rem;
  height: 30rem;
  width: 25rem;
  overflow: scroll;
  border-radius: 1rem;
  color: #fff;
  background-color: #9c9e2b;
  background-image: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.7));
}

.history-container::-webkit-scrollbar,
.lyrics-container::-webkit-scrollbar {
  display: none;
}

.lyrics-title {
  margin-top: 1.5rem;
  margin-left: 2rem;
  font-size: 1.2rem;
  font-weight: 200;
  font-family: "Poppins", sans-serif;
}

.lyrics {
  flex-wrap: wrap;
  margin: 1rem 2rem 2rem 2rem;
  font-size: 1.3rem;
  font-weight: 600;
}

.overlay {
  position: absolute;
  background-image: linear-gradient(180, transparent, #111);
}

.history-container {
  box-shadow: rgba(85, 85, 85, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  margin-top: 7rem;
  height: 25rem;
  width: 25rem;
  overflow: scroll;
  border-radius: 1rem;
  color: #fff;
  /* background-image: url(assets/historyphoto.jpg); */
  background-size: cover;
  background-position: center center;
  margin-bottom: 5rem;
}

.history-title {
  margin-top: 1.5rem;
  margin-left: 2rem;
  font-size: 1.2rem;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
}

.history-inner-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.5);
}

.history {
  flex-wrap: wrap;
  margin: 2rem 2rem 2rem 2rem;
  font-size: 0.95rem;
  font-weight: 300;
  color: rgb(224, 221, 221);
}

.slider {
  width: 25rem;
  accent-color: #dee137;
}

@media (max-width: 1100px) and (min-width: 500px) {
  .main-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 0;
  }

  .first-container {
    position: relative;
  }

  .second-container {
    display: none;
  }

  .content-container {
    position: relative;
  }

  .song-image {
    margin-top: 5rem;
  }

  .music-editor {
    margin-top: 2rem;
  }

  .song-data {
    margin-top: 5rem;
    margin-left: 0;
  }

  .info-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .lyrics-container {
    margin-left: 0;
  }

  .history-container {
    margin-left: 0;
  }

  .song-title {
    text-align: center;
    font-size: 3rem;
  }

  .song-artist {
    text-align: center;
  }
}

@media (max-width: 500px) {
  .main-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 0;
  }

  .first-container {
    position: relative;
  }

  .second-container {
    display: none;
  }

  .content-container {
    position: relative;
  }

  .song-image {
    width: 20rem;
    height: 20rem;
    margin-top: 5rem;
  }

  .music-editor {
    margin-top: 2rem;
  }

  .slider {
    width: 20rem;
    accent-color: #dee137;
  }

  .song-data {
    margin-top: 2rem;
    margin-left: 0;
  }

  .info-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .lyrics-container {
    margin-left: 0;
    margin-top: 4rem;
    height: 30rem;
    width: 22rem;
  }

  .history-container {
    margin-top: 4rem;
    height: 22rem;
    width: 22rem;
  }

  .song-title {
    text-align: center;
    font-size: 2rem;
  }

  .song-artist {
    font-weight: 200;
    font-size: 1.2rem;
    text-align: center;
  }

  .music-editor {
    margin-top: 0.45rem;
  }

  .selector {
    margin: 20px;
    width: 20px;
    height: 20px;
  }

  .play-button {
    font-size: 1rem;
    width: 40px;
    height: 40px;
    padding: 10px;
    border: none;
  }

  .play-image {
    width: 20px;
    height: 20px;
  }
}
